import React from "react"
//import Img from "gatsby-image"
import { useStaticQuery, graphql } from "gatsby"
import { Link } from "gatsby"

//== NAVIGATION ==//

export default () => {
    const data = useStaticQuery(graphql`
        query Icon{
            image: file(relativePath: {eq: "icon.png"}) {
                id
                childImageSharp {
                    fluid(maxWidth: 30) {
                        src
                        originalName
                        originalImg
                    }
                    original {
                        width
                        height
                    }
                }
            }
        }
    `
    )
    return (
        <nav>
            <div class="navi">

                <Link to="/"><img src={data.image.childImageSharp.fluid.src} alt={data.image.childImageSharp.fluid.originalName} className="logo" /></Link>

                <Link to="#" class="navi-item">

                </Link>
                
                <Link to="#" class="navi-item">

                </Link>

                <Link to="/" className="navi-item">
                    HOME
                </Link>

                <Link to="/about/" className="navi-item">
                    ABOUT
                </Link>

                <a id="email" className="navi-item" style={{ float: `right`, marginTop: `5px` }} >
                    <b style={{ color: `#b3a5c1` }}>GUIDE</b>
                </a>



            </div>
        </nav>
    )

}
