/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"
import NAV from "../components/nav"
import Breadcrumb from "../components/header"

//== Components ==//
import Header from "./header"

//== CSS ==//
//import "../styles/css/layout.css"
import "../styles/css/bulma.css"
import "../styles/css/custom.css"

//== JS ==//



const Layout = ({ children }) => {

  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <>
      <Header siteTitle={data.site.siteMetadata.title} />
      <NAV />
      <Breadcrumb homeName="GONG CHA PAL" />
      
      <h5 style={{ textAlign: `center`, fontWeight: `400`, color: `#fff`, marginBottom: `30px`}}>A guide to ordering tea.</h5>
      <div className='main'>
        <div className='blog-roll'>
         
          <main>{children}</main>

          <footer>
            Â© 2020 <a style={{ color: '#fff' }} >Gong Cha PAL</a> Built with
            {` `}
            <a style={{ color: '#fff' }} href="https://www.gatsbyjs.org">Gatsby</a>
          </footer>
          
        </div>
      </div>
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout


